h1, h2, h3, h4, h5, h6 {
  text-shadow: $textShadow;
}

// Typed Cursor
.typed-cursor, .message {
  font-size: 2em;
  line-height: 1.2em;
  color: $mrRobotPrimaryColor;
  display: inline-block;
}

.primaryColorText {
  color: $mrRobotPrimaryColor;
}

.message-container {
  min-width: calc(100% - 20%);
  display: inline-block;
}

.typed-cursor {
  opacity: 1;
  -webkit-animation: blink 0.7s infinite;
  -moz-animation: blink 0.7s infinite;
  animation: blink 0.7s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.no-link {
  font-style: italic;
  color: $mrRobotPrimaryColor;
  &:hover, &:active {
    text-decoration: none;
    color: $mrRobotPrimaryColor;
    cursor: default;
  }
}
